.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.myrec {
  /* display: flex;
  justify-content: center; */
  margin: 27px 16px;
}
.myreceipe {
  margin: 10px 0px;
}
.infobox {
  display: flex;
  margin: 40px 20px;
  font-family: sans-serif;
  justify-content: space-around;
}
.infobox img{
  width: 40%;
  height: 250px;
}
.mealsteps {
  margin: 20px 14px;
  font-family: system-ui;
  padding: 10px 24px;
  border: solid 2px black;
  font-size: 21px;
  font-weight: 300;
}
#infoh1{
  text-align: center;
  margin: 23px 10px;
  font-family: system-ui;
  font-weight: 300;
}
.summary {
    border: solid 2px black;
    margin: 40px 14px;
    padding: 22px 20px;
}
.summary h1{
  margin: 0px 0px 18px 0px;
  font-family: system-ui;
  font-weight: 300;
}
.summary a{
  text-decoration: none;
  color: black;
}
li {
  list-style: none;
}
#cantfind {
  font-size: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-family: system-ui;
  text-align: center;
}